    border-radius: 10px;
    text-align: center;
    height: 75%;
    width: 90%;
    color: black;
}

.resumeBG {
    background-image: url(/static/media/rectangle_24.dfd26429.png);
    
    border-radius: 10px;
    
    
    
}

.selfIntroduction span {
    top: 0px;
    transition: 3.3s;
    transform: translate(-50%, -50%);
}
.selfIntroduction span:hover {
    -webkit-animation: scrollDown 1s linear;
            animation: scrollDown 1s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

