/* Containers */
div#applyBackground {
    margin-top: 0%;
    overflow-x: hidden;
    
    width: inherit;
}

div#applyContainer {
    position: relative;
    
    background-image: url(/static/media/apply_page.556dae70.png);
    background-size: 100% 100%;
    margin: auto;
    display: grid;
    grid-template-areas: 
        "resume"
        "comment"
        "submit";
}

div#applyContainer {
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: 
        "resume comment"
        "submit submit";
}

/* Resume Upload Section */
div#resumeContainer {
    grid-area: resume;
    display: grid;
    grid-template-areas: 
        "ResumeLabel"
        "ResumeUpload";
    margin-left: 3%;
    margin-top: 10vw;
}

#resume-label {
    grid-area: ResumeLabel;
    color: white;
    font-family: Racing Sans One;
    font-size: 5vw;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

#resumePath {
    grid-area: ResumeUpload;
}

/* Comment Section */
div#commentContainer {
    grid-area: comment;
    display: grid;
    grid-template-areas: 
        "CommentLabel"
        "CommentBox";
    margin-top: 10vw;
}

#comment-label {
    grid-area: CommentLabel;
    color: white;
    font-family: Racing Sans One;
    font-size: 5vw;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

#comments {
    grid-area: CommentBox;
    height: 5vw;
    width: 30vw;
}

/* Submit Button */
button#submitBtn {
    grid-area: submit;
    width: 80%;
    height: 3vw;
    margin-top: 10vw;
    margin-left: 5%;
}

/* After Applying */

div#applied-container {
    margin: auto;
    margin-top: 1%;
    display: flex;
    overflow: hidden;
    padding: 5%;
}

#applied-text {
    font-family: Racing Sans One;
    margin-left: -100%;
    -webkit-animation: SlideFromLeft 1s;
            animation: SlideFromLeft 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

#applied-img {
    -webkit-animation: Shake 1s;
            animation: Shake 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

@-webkit-keyframes SlideFromLeft {
    50% {
        margin-left: 0%;
    }
    100% {
        margin-left: 0%;
    }
}

@keyframes SlideFromLeft {
    50% {
        margin-left: 0%;
    }
    100% {
        margin-left: 0%;
    }
}

@-webkit-keyframes Shake {
    20% { transform: translate(-2px, 0);}
    30% { transform: translate(2px, 0);}
    40% { transform: translate(-2px, 0);}
    50% { transform: translate(2px, 0);}
    60% { transform: translate(-2px, 0);}
    70% { transform: translate(2px, 0);}
    80% {transform: scale(1.2);}
    100% { transform: translate(2px, 0);  transform: scale(1);}
}

@keyframes Shake {
    20% { transform: translate(-2px, 0);}
    30% { transform: translate(2px, 0);}
    40% { transform: translate(-2px, 0);}
    50% { transform: translate(2px, 0);}
    60% { transform: translate(-2px, 0);}
    70% { transform: translate(2px, 0);}
    80% {transform: scale(1.2);}
    100% { transform: translate(2px, 0);  transform: scale(1);}
}
