    line-height: 50px;
}
.line:first-child {
    -webkit-animation: slide 20s infinite;
            animation: slide 20s infinite;
}

@-webkit-keyframes slide {
    0% {
        margin-top: 0px;
    }
    2% {
        margin-top: 0px;
    }
    16% {
        margin-top: -50px;
    }
    18% {
        margin-top: -50px;
    }
    33% {
        margin-top: -100px;
    }
    35% {
        margin-top: -100px;
    }
    66% {
        margin-top: -150px;
    }
    82% {
        margin-top: -200px;
    }
    100% {
        margin-top: -250px;
    }
}

